.iswitch{
	display: inline-block;
	position: relative;
	background-color: #4FAE52;
	padding: .5%;
	color: #fff;
	min-width: 80px;
	height: 30px;
	border-radius: 5px;
	border: 1px solid #4FAE52;
	-webkit-transition: all .5s;
	transition: all .5s;
}
.iswitch.close{
	background-color: #8F8F8F;
	border-color: #8F8F8F;
}
.iswitch .block{
	width: 50%;
	background-color: #fff;
	position: absolute;
	top: 0px;
	left: 0px;
	bottom: 0px;
	z-index: 1;
	border-radius: 5px;
	-webkit-transition: all .5s;
	transition: all .5s;
}
.iswitch .block.open{
	-webkit-transform: translateX(100%);
	        transform: translateX(100%);
}
.iswitch .text{
	position: absolute;
	top: 0;
	right: 0;
	bottom: 0;
	left: 0;
	z-index: 2;
	display: -webkit-box;
